.o-search-nav {
  flex: 1 0 40%;
  margin: $grid-gutter;
  padding: $grid-gutter;
  min-width: 320px;
  @media #{$max-six-forty} {
    order: -1; } }

.c-search-nav__heading {
  text-align: center;
  font-family: $serif;
  margin-bottom: -10px; }

.c-search-nav__description {
  text-align: center;
  font-style: italic; }

.o-search-input-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 90%;
  margin: 0 auto; }

.c-search-input {
  display: flex;
  flex-direction: column;
  align-items: center;
  @include type-setting(0, $sans);
  letter-spacing: 0.5px;
  width: 100%;
  margin: 10px auto;
  @media #{$min-six-forty} {
    width: 80%; }
  input {
    max-width: 100%;
    padding: $grid-gutter;
    @include type-setting(0, $sans);
    letter-spacing: 0.5px; }
  label {
    padding: $grid-gutter 0; } }

// Variations
.o-search-nav--archive {
  .c-search-input {
    @media #{$min-six-forty} {
      max-width: 60%; } }
  .c-search-input__form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center; } }

.o-search-nav--sidebar {
  flex: 1 0 30%;
  min-width: 320px;
  margin: $grid-gutter;
  padding: $grid-gutter; }
