.c-content-module--books {
	flex-direction: row;
	flex-wrap: wrap;
	align-content: center;
	margin-bottom: 0;
	@media #{$max-six-forty} {
		padding-top: 0; } }

.books-image {
	flex: 1 0 33%;
	flex-grow: 1;
	margin: 0 5%;
	min-width: 150px;
	@media #{$min-six-forty} {
		max-width: 100%; }
	img {
		display: block;
		max-width: 100%;
		max-height: 300px;
		margin: 0 auto 20px;
		@media #{$min-six-forty} {
			max-height: 350px;
			margin: 0 auto 0; } } }


.books-info {
	flex: 1 0 33%;
	margin: 0 5%;
	min-width: 280px; }

.books-title {
	@include type-setting(3);
	display: block;
	@media #{$max-nine-sixty} {
		@include type-setting(2); }
	&:hover {
		color: c('primary');
		text-decoration: none; } }

.books-author {
	@include type-setting(0, $serif, c('secondary'));
	display: block;
	margin: 0; }

.books-description {
	@include type-setting(-1);
	padding: $grid-gutter 0;
	border-bottom: 1px solid c('spacer');
	line-height: 2rem; }

.books-extra {
	flex: 1 0 100%;
	text-align: right;
	padding-right: 5%; }

.c-books-button {
	display: inline-block;
	background-color: rgba(0,0,0,0);
	margin-top: 0;
	a {
		height: 100%;
		width: 100%; }
	&:hover {
		a {
			color: c('white');
			text-decoration: none; } } }
