.c-article__header {
  max-width: 80vw;
  border-bottom: 1px solid c('spacer');
  @supports (display: grid) {
    grid-column: 2;
    max-width: none; } }

.c-article__head-image {
  max-width: 100%;
  height: 50vh;
  max-height: 400px;
  padding: 20px 0;
  border-top: 1px solid c('spacer');
  img {
    width: 100%;
    height: 100%;
    object-fit: cover; } }

.c-article__info {
  padding-bottom: 8px; }

.c-article__category {
  font-family: "Proxima Nova", $sans;
  font-size: 1.7rem;
  color: c('primary');
  margin: 0; }

.c-article__info--title {
  margin: 1.3rem 0 0.8rem;
  font-size: 4.8rem;
  line-height: 4.8rem;
  .lock-icon {
    font-size: 3rem; } }

.c-article__info--author {
  margin: 0;
  font-size: 1.4rem;
  font-family: "Proxima Nova", $sans;
  font-weight: normal;
  display: inline;
  margin-right: 10px;
  .c-author-link {
    @include type-setting(1, $sans, c('black'));
    letter-spacing: 0.5px;
    font-weight: 600; } }

.c-article__info--date {
  font-size: 1.4rem;
  font-family: "Proxima Nova", $sans;
  color: c('secondary');
  display: inline;
  font-weight: normal;
  a {
    color: c('secondary');
    border-left: 1px solid c('spacer');
    padding-left: 10px;
    margin-left: 10px; }
  a:hover {
    color: c('accent'); } }

.c-article__social-icons {
  position: absolute;
  top: 180px;
  left: $grid-unit / 1.8;
  opacity: 0.1;
  @media #{$max-nine-sixty} {
    display: none; }
  &:hover {
    opacity: 1; }
  .c-social-icons {
    display: flex;
    flex-direction: column;
    svg {
      width: 30px;
      height: 30px;
      &:hover {
        fill: c('accent'); } } }
  .c-social-icons--hidden {
    display: flex;
    flex-direction: column;
    position: relative;
    transform: translateY(-32px);
    transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
    pointer-events: none;
    svg {
      position: absolute;
      opacity: 0;
      pointer-events: none; }
    @for $i from 1 through 2 {
      a {
        transition: transform #{$i * 0.3s} ease-in-out; }
      svg {
        transition: opacity #{$i * 0.3s} ease-in-out; } } }

  .c-social-icons--hidden.social-visible {
    pointer-events: auto;
    opacity: 1;
    @for $i from 1 through 2 {
      a:nth-of-type(#{$i}) {
        transition: transform #{$i * 0.3s} ease-in-out;
        transform: translateY($i * 32px); }
      svg {
        pointer-events: auto;
        opacity: 1; } } }

  .social-expand {
    svg {
      transition: transform 0.3s ease-in; }
    &:hover {
      svg {
        cursor: pointer;
        fill: c('accent'); } } }

  .social-expand.social-visible {
    svg {
      transform-origin: center center;
      transform: rotate(45deg); } } }

.c-article__social-icons.is-scrolling {
  position: fixed;
  top: 80px; }
