// Full Width Headline - Layout & Background
%fwh {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  min-height: 450px;
  justify-content: space-around;
  align-items: center;
  position: relative;
  overflow: hidden; }

@mixin fwh-color($background: c('black')) {
  background-color: $background; }

%fwh__background {
  display: none;
  @supports (mix-blend-mode: multiply) {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    img {
      min-width: 100%;
      min-height: 100%; } } }

@mixin fwh__background-image($opacity: 0.6, $blend: multiply, $filter: blur(10px)) {
  img {
    opacity: $opacity;
    mix-blend-mode: $blend;
    filter: $filter; } }

// Full Width Headline - Type
%fwh__heading {
  max-width: 60%;
  flex: 1 0 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 10%;
  z-index: 2;
  min-width: 320px; }

@mixin title-oversize($title-color: c('white')) {
  @include type-setting(3, $serif, $title-color);
  font-weight: 400;
  @media #{$min-nine-sixty} {
    @include type-setting(6, $serif, $title-color); } }

@mixin vertical-buttons($flex-width) {
  flex: 1 0 $flex-width;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 2;
  min-width: 300px;
  @media #{$min-six-forty} {
    flex-direction: row; }
  @media #{$min-nine-sixty} {
    flex-direction: column; } }

.c-product-button--dark {
  @include o-button(rgba(0,0,0,0), c('white'));
  margin: 10px auto;
  max-width: 100px;
  text-align: center;
  &:hover {
    color: c('black');
    text-decoration: none; } }
