.page-template-sf-book-page {
  @media (min-width: 801px) {
    .u-page-wrapper--secondary-header {
      margin-top: 70px; } } }

.c-product {
  section {
    display: flex;
    flex-wrap: wrap;
    padding: 50px 0;
    align-items: center;
    &:nth-of-type(2n+4) {
      background-color: c('background-light'); } } }

.c-product-headline {
  @extend %fwh;
  @include fwh-color; }

.c-product-headline__background {
  @extend %fwh__background;
  @include fwh__background-image; }

.c-product-headline__headings {
  @extend %fwh__heading; }

.c-product__title {
  @include title-oversize; }

.c-product__author {
  @include type-setting(2, $serif, c('white'));
  font-weight: 400; }

.c-product-headline__buttons {
  @include vertical-buttons(30%);
  padding-top: 1.3rem;
  @media #{$min-nine-sixty} {
    padding-top: 6.3rem; } }

.c-product-button--read {
  height: auto;
  max-width: 250px; }

.c-product-gloss {
  @media (max-width: 768px) {
    justify-content: center;
    .c-product__image {
      margin: 0 0 0 0; }
    .c-product__pullquote {
      padding-top: 30px; }
    .c-product__pullquote--quote {
      font-size: 2rem;
      line-height: 2.8rem; }
    .c-product__pullquote--attrib {
      text-align: right; } }
  &:after {
    content: '';
    display: block;
    position: relative;
    top: 60px;
    left: 15%;
    width: 70%;
    height: 2px;
    background-color: c('spacer');
    @media (max-width: 768px) {
      left: 0; } } }


.c-product__image {
  flex: 1 0 30%;
  margin: 0 5% 0 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 30%;
  min-width: 280px;
  img {
    max-height: 90%;
    max-width: 100%;
    max-width: 300px;
    object-fit: contain;
    box-shadow: 10px 20px 30px rgba(0,0,0,0.25); } }

.c-product__magazine-image {
  flex: 1 0 30%;
  margin: 0 5% 0 10%;
  background-color: c('background-light');
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 10px 20px 30px rgba(0,0,0,0.25);
  max-width: 30%;
  min-width: 280px;
  min-height: 300px;
  max-height: 600px;
  img {
    max-height: 90%;
    max-width: 100%;
    max-width: 300px;
    object-fit: contain; } }

.c-product__pullquote {
  flex: 1 0 40%;
  margin: 0 10% 0 5%; }

.c-product__pullquote--quote {
  @include type-setting(1, $serif);
  font-style: italic;
  @media #{$min-nine-sixty} {
    @include type-setting(2, $serif); } }

.c-product__pullquote--attrib {
  font-weight: 800;
  @include type-setting(1, $sans, c('secondary')); }

.c-product__excerpt {
  @media (max-width: 768px) {
    padding: 0 5%; } }

.c-product-info {
  max-width: 700px;
  margin: 0 auto 0; }

.c-table-of-contents {
  margin: 56px auto 0;
  text-align: center;
  h3 {
    @extend .heading-4;
    padding-top: $baseline;
    &:after {
      content: '';
      display: block;
      position: relative;
      height: 2px;
      width: 30%;
      top: 0;
      left: 35%;
      background-color: c('accent'); } } }

.c-magazine-intro {
  flex: 1 0 30%;
  margin: 0 10% 0 5%;
  ul.c-product__purchase-info__list {
    padding: 20px 0;
    border-bottom: 1px solid c('spacer');
    li {
      text-align: left;
      @include type-setting(1, $serif, c('black')); } }
  p {
    @include type-setting(1, $serif, c('black'));
    font-style: italic; } }

.c-product__author-bio__text {
  flex: 1 0 50%;
  padding: 0 5 0 5%;
  @media #{$min-nine-sixty} {
    max-width: 55%;
    padding: 0 10% 0 2%; }
  .heading-4 {
    text-align: center;
    color: c('secondary');
    font-family: $sans;
    a {
      color: c('secondary'); }
    @media #{$max-six-forty} {
      font-size: 2rem; }
    @media #{$min-nine-sixty} {
      width: 100%; } }
  p {
    @media #{$max-nine-sixty} {
      padding: 0 5%; } } }

.c-product__author-bio__image {
  flex: 1 0 30%;
  @media #{$min-nine-sixty} {
    max-width: 30%;
    margin: 0 0 0 15%; }
  img {
    max-width: 300px;
    margin: 0 10px; } }

.c-product__purchase-info {
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  padding: 80px 0;
  a {
    @include type-setting(2, $sans, c('primary'));
    line-height: 3.2rem;
    padding: 10px 32px;
    height: 56px;
    &:hover {
      text-decoration: none; } } }

.c-product__pub-info {
  display: flex;
  line-height: 1.8rem;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 30px;
  margin: 0 5% 0 10%;
  flex: 1 0 30%;
  .heading-4 {
    flex: 1 0 100%;
    text-align: center;
    color: c('secondary');
    font-family: $sans; }
  p {
    flex: 1 0 50%;
    text-align: center;
    border: 1px solid c('spacer');
    padding: 10px 0;
    &:nth-of-type(even) {
      border-left: none; } } }

.c-product__purchase-options {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex: 1 0 70%;
  .c-purchase-option {
    display: flex;
    flex-direction: column;
    flex: 1 0 50%;
    align-items: center;
    &:first-child {
      border-right: 1px solid c('spacer'); }
    a {
      margin: 20px auto; } } }

.c-product-price {
  font-size: 6rem;
  color: c('black');
  border-bottom: 2px solid c('secondary');
  padding: 40px;
  margin-bottom: 20px; }

.c-product__purchase-info__list {
  @include type-setting(-1, $sans, c('secondary'));
  list-style: none;
  -webkit-padding-start: 0;
  text-align: center;
  li {
    color: c('secondary'); } }

.c-product__blurb-group {
  flex-direction: column;
  align-items: center;
  .heading-3 {
    color: c('secondary');
    @media #{$max-nine-sixty} {
      padding: 0 5%; } } }

.c-product__blurb {
  max-width: 700px;
  margin-bottom: 30px;
  color: c('background-dark');
  @media #{$max-nine-sixty} {
    padding: 0 5%; }
  &:after {
    content: '';
    display: block;
    position: relative;
    top: 10px;
    left: 15%;
    width: 70%;
    height: 1px;
    background-color: c('spacer', 1, black); } }

.c-product__blurb-attrib {
  text-align: right; }
