.c-form-input {
  @include type-setting(0, $sans);
  margin: 0;
  margin-left: $grid-gutter;
  height: 36px;
  width: 220px;
  padding: 4px 3px 2px 5px; }

.c-form-button {
  @include type-setting(0, $sans);
  margin: 0;
  margin-left: -1px;
  border: none;
  background-color: c('spacer');
  border-radius: 0 2px 2px 0;
  &:hover {
    background-color: c('primary');
    color: c('white');
    box-shadow: none;
    cursor: pointer; } }
