.c-article__body {
  @include type-setting(0);
  padding: 24px 0 20px;
  max-width: 670px;
  p, form {
    @include type-setting(0);
    margin-bottom: 1rem;
    @supports (display: grid) {
      grid-column: 2;
      @media (min-width: 400px) {
        padding: 0 20px; }
      @media (min-width: 640px) {
        padding: 0 40px; } } }
  @supports (display: grid) {
    grid-column: 1 / -1;
    width: 100%;
    display: grid;
    max-width: none;
    grid-template-columns: 1fr minmax(300px, 780px) 1fr;
    figure {
      margin: 0 -30px; }
    p img {
      margin-left: -70px; } }
  figcaption {
    font-family: "Proxima Nova", $sans;
    font-size: 1.2rem;
    color: c('secondary'); }
  #loginform {
    background-color: c('spacer');
    border-radius: 2px;
    p {
      font-family: "Proxima Nova", $sans;
      font-size: 1.3rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 2rem 0; }
    label {
      width: 90%;
      text-align: left; }
    input {
      background-color: white;
      border: none;
      padding: 8px 4px;
      border-radius: 3px;
      width: 90%;
      &.button {
        background-color: c('primary');
        color: white; }
      &.button:hover {
        background-color: rgba(c('primary'), 0.8); } }
    .login-remember {
      label, input {
        width: auto; } } }
  blockquote {
    background-color: rgba(c('accent'), 0.1);
    max-width: 80%;
    margin: 0 auto;
    p {
      font-size: 85% !important;
      line-height: 140% !important; } } }


.c-article__footer {
  font-style: italic;
  border-top: 1px solid c('spacer');
  max-width: 700px;
  padding: 0 $grid-gutter $grid-unit;
  p {
    @include type-setting(-1); }
  @supports (display: grid) {
    grid-column: 2;
    max-width: none;
    padding: 0 40px; } }

.c-sidebar--aside {
  position: fixed;
  right: 0;
  top: 100px;
  border: none;
  border-left: 1px solid c('spacer');
  padding: 0 20px;
  max-width: 16vw;
  transform: translateX(301px);
  transition: transform 0.65s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  will-change: transform;
  @media (max-width: 1050px) {
    display: none; }
  &.visible {
    transform: translateX(0); }
  .c-sidebar-section__readmore {
    margin-bottom: 25px; }
  .c-sidebar-section__title {
    border: none;
    margin: 0 0 1rem;
    padding: 0; }
  .c-sidebar-section__readmore--thumb {
    width: 90%;
    height: 100px;
    object-fit: cover; }
  .c-sidebar-section__readmore--title, .c-sidebar-section__readmore--author {
    font-family: $sans;
    font-weight: 600;
    color: c('black');
    font-size: 1.3rem;
    line-height: 1.6rem;
    margin: 0; }
  .c-sidebar-section__readmore--author a {
    color: c('secondary');
    &:hover {
      color: c('primary'); } } }

