.o-underlay {
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.4);
  opacity: 0;
  will-change: opacity;
  transition: opacity 0.3s cubic-bezier(0,0,0.3,1);
  pointer-events: none; }

.o-underlay.side-nav--visible {
  opacity: 1;
  pointer-events: auto; }

.o-underlay--search {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.85);
  opacity: 1;
  will-change: opacity;
  transition: opacity 0.3s cubic-bezier(0,0,0.3,1); }
