.c-publication-wrapper {
  display: flex;
  max-width: 100%;
  flex-wrap: wrap; }

.c-magazine-archive__info {
  border-bottom: 1px solid c('spacer');
  margin-bottom: 10px;
  padding-bottom: 10px; }

.c-publication-module {
  flex: 1 0 33%;
  background-color: c('white') !important;
  .c-publication__image {
    height: 355px;
    max-height: 355px;
    margin-bottom: 10px;
    img {
      max-height: 100%;
      object-fit: contain; } }
  .c-content-text {
    width: 100%;
    text-align: center;
    .author {
      display: block;
      font-size: 1.6rem;
      font-family: "Proxima Nova", $sans;
      margin-top: 12px;
      color: c('secondary'); } }
  .c-mag-issue {
    @include type-setting(0, $sans, c('secondary'));
    &:after {
      content: '';
      display: block;
      position: relative;
      top: 0;
      left: 10%;
      height: 1px;
      width: 80%;
      background: c('spacer'); } }
  .c-content-title {
    @include type-setting(2, $serif, c('black'));
    font-style: italic;
    display: inline-block;
    clear: both;
    margin: 0; } }

.c-magazine-archive__info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  .c-magazine-archive__info--text {
    flex: 1 0 60%;
    min-width: 300px;
    max-width: 640px;
    @media #{$max-six-forty} {
      padding: 0 10px; }
    h2 {
      @include type-setting(2, $sans);
      margin-top: 0;
      @media #{$min-six-forty} {
        @include type-setting(5, $sans); }
      &:after {
        content: '';
        display: block;
        position: relative;
        height: 3px;
        width: 100%;
        top: 0;
        left: 0;
        background-color: c('primary');
        @media #{$min-six-forty} {
          top: -10px; } } } } }
.c-product-category {
  width: 100%; }
.c-products--magazines {
  padding: 0 0 0 0;
  display: flex;
  flex-wrap: wrap;
  .c-publication__image {
    height: 260px;
    max-height: 260px; }
  .c-content-title {
    font-style: normal; } }

.c-publication-wrapper--anthologies {
  .c-content-text {
    margin-top: 10px;
    padding: 10px;
    border-top: 1px solid c('spacer'); } }
