.c-sidebar {
  padding: 0;
  @include type-setting(-1, $serif);
  flex: 1 0 30%;
  min-width: 280px;
  max-width: 400px;
  border-left: 1px solid c('spacer');
  @media #{$min-six-forty} {
    padding: $grid-gutter; } }

.c-sidebar--bottom {
  max-width: 100%;
  margin: 0px 0px 100px;
  flex: 1 0 100%;
  display: flex;
  flex-wrap: wrap;
  border-left: none;
  .widget {
    min-width: 250px;
    max-width: 100%;
    @media #{$min-six-forty} {
      max-width: 40%;
      margin: 0 5%;
      flex: 1 0; } }

  .c-sidebar-section {
    flex: 1 0 30%;
    margin: 0 $grid-gutter;
    min-width: 280px; }

  .widget-title {
    display: none; } }

.c-subscribe-section {
  height: $baseline * 10;
  img {
    max-height: $baseline * 8; } }


.c-sidebar-section__title {
  @include type-setting(0, $sans);
  margin: $baseline 0 $grid-gutter;
  padding: 0 $grid-gutter;
  border-bottom: 1px solid c('spacer'); }

.c-sidebar-section__item {
  display: flex;
  align-content: flex-start;
  max-width: 100%;
  height: 135px;
  align-items: center;
  padding: $grid-gutter;
  @media #{$min-six-forty} {
    align-items: flex-start; } }

.c-sidebar-section__item--thumb {
  height: 70px;
  margin: 0 20px 0 0;
  flex: 1 0 20%; }

.c-sidebar-section__item--info {
  flex: 1 0 80%;
  display: flex;
  flex-wrap: wrap; }

.c-sidebar-section__item--title {
  flex: 1 0 100%;
  @include type-setting(1, $sans);
  line-height: 2.2rem;
  padding: 0;
  margin: 0 0 -10px 0; }

.c-sidebar-section__item--author {
  @include type-setting(-1, $sans, c('secondary'));
  padding: 0 10px 0 0;
  border-right: 1px solid c('spacer');
  margin-bottom: 0; }

.c-sidebar-section__item--date {
  @include type-setting(-1, $sans, c('secondary'));
  padding: 0 0 0 10px;
  margin-bottom: 0; }

.c-sidebar-section__input {
  display: flex;
  align-items: center; }

.c-sidebar-item--magazine {
  display: flex;
  flex-wrap: wrap;
  .c-sidebar-section__title {
    flex: 1 0 100%; } }

.c-sidebar-section__latest-mag {
  align-items: center; }

.c-sidebar-item--mag-cover {
  height: 180px;
  max-height: 180px;
  padding: 10px;
  flex: 1 0 50%;
  img {
    max-height: 100%;
    max-width: 100%;
    object-fit: contain; } }

.c-sidebar-item--mag-title {
  flex: 1 0 50%;
  @include type-setting(1, $serif, c('black'));
  padding: 10px; }

.c-sidebar-section__item--description {
  max-width: 80%;
  p {
    @include type-setting(0, $sans, c('secondary'));
    font-weight: 400 !important; } }

.widget_facebook_likebox, .widget_twitter_timeline {
  margin: 65px 15px; }

.widget_search {
  .widget-title {
    @include type-setting(0, $sans);
    margin: $baseline 0 0;
    padding: 0 $grid-gutter;
    border-bottom: 1px solid c('spacer'); }
  .search-form {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 10px; }
  .screen-reader-text {
    visibility: hidden;
    position: fixed; }
  .search-field {
    width: 240px;
    font-family: $sans;
    font-size: 14px;
    height: 28px; }
  .search-submit {
    @extend %button;
    @include o-button;
    margin-left: 5px; } }

.widget_mc4wp_form_widget {
  .widget-title {
    @include type-setting(0, $sans);
    margin: $baseline 0 0;
    padding: 0 $grid-gutter;
    border-bottom: 1px solid c('spacer'); } }

.mc4wp-form-basic {
  margin: 0 0 0 0 !important;
  margin-top: 0 !important; }

.mc4wp-form-fields {
  p {
    padding: 0 10px;
    display: flex;
    align-items: center;
    margin: 0 0 0 0;
    label {
      visibility: none;
      position: fixed; }
    input[type="email"] {
      display: inline-block;
      width: 240px;
      font-family: $sans;
      font-size: 14px;
      height: 28px; }
    input[type="submit"] {
      display: inline-block;
      @extend %button;
      @include o-button;
      margin-left: 5px; } } }
