.u-highlight {
  color: c('highlight') !important;
  &:hover {
    color: c('accent') !important; } }

.u-italic {
  font-style: italic !important; }

.u-bold {
  font-weight: bold !important; }

.add_to_cart_inline {
  border: none !important;
  .add_to_cart_button {
    @extend %button;
    @include o-button;
    padding: 10px 16px 8px; } }

// Store Notice
.woocommerce-store-notice, p.demo_store {
  background-color: #fff !important;
  color: cornflowerblue !important;
  padding: 2rem 15vw !important;
  display: block;
  font-size: 1.8rem;
  line-height: 2.6rem;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0;
  width: 100%;
  text-align: center;
  z-index: 99998;
  box-shadow: 0 1px 1em rgba(0,0,0,.2); }

.woocommerce-store-notice a, p.demo_store a {
  color: #002465 !important;
  &:last-of-type {
    color: c('secondary');
    font-size: 1rem; } }
