.c-primary-header {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 30px auto;
  max-width: 96vw;
  @media #{$min-six-forty} {
    width: calc(100vw - 80px);
    max-width: 1600px; }
  @media (max-width: 700px) {
    margin: 10px auto; } }

.c-primary-header__sub-ad {
  display: none;
  @media (min-width: 1179px) {
    position: absolute;
    display: block;
    top: -10px;
    left: 0;
    max-width: 17%;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      max-height: 110px; } } }


.c-primary-header__title {
  flex: 1 0 100%;
  padding-bottom: 0.5rem;
  display: flex;
  justify-content: center; }

#c-primary-header__wordmark {
  max-height: 108px;
  max-width: 100%;
  max-width: 96vw;
  @media #{$min-six-forty} {
    max-width: 76vw; }
  @media #{$max-nine-sixty} {
    transform: scale(0.8); }
  @media (max-width: 700px) {
    max-height: 50px; } }


.c-primary-header__nav {
  z-index: 100;
  @media #{$max-six-forty} {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    pointer-events: none; }
  @media #{$min-six-forty} {
    width: calc(100vw - 80px);
    margin-top: -20px; } }

.c-primary-header__nav--container {
  @media #{$max-six-forty} {
    padding-top: 50px;
    position: relative;
    width: 85vw;
    max-width: 420px;
    background: c('white');
    height: 100vh;
    box-shadow: 2px 0 12px rgba(0,0,0,0.4);
    transform: translateX(-102%);
    display: flex;
    flex-direction: column;
    will-change: transform; } }

.c-primary-header__nav--menu {
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
  width: 100%;
  -webkit-padding-start: 0px;
  border-top: 1px solid c('spacer');
  border-bottom: 1px solid c('spacer');
  @media #{$max-six-forty} {
    padding-top: 32px;
    flex: 1;
    list-style: none;
    padding: 0;
    margin: 0;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    max-height: 550px; }
  @media #{$min-six-forty} {
    flex-direction: row;
    padding: 0 4%;
    align-items: center;
    flex: 1 0 100%;
    justify-content: center; }
  @media #{$min-nine-sixty} {
    font-size: 1.4rem; }
  @media #{$max-nine-sixty} {
    font-size: 1.3rem; } }

.side-nav--visible {
  pointer-events: auto; }

.c-primary-header__nav--menu.side-nav--visible {
  transform: translateX(-15vw); }

.c-primary-header__nav--container.side-nav--animatable {
  transition: transform 0.33s cubic-bezier(0,0,0.3,1); }

.c-primary-header__nav--container.side-nav--animatable.side-nav--visible {
  transition: transform 0.33s cubic-bezier(0,0,0.3,1); }

.c-primary-header__nav--container.side-nav--visible {
  transform: none; }

.c-primary-header__nav--menu {
  li {
    font-family: "proxima-nova", $sans;
    font-weight: 400;
    letter-spacing: 0.5px;
    font-size: 1.3rem;
    list-style: none;
    padding: 8px 10px;
    width: 80%;
    margin: 0 auto;
    flex: 1;
    text-align: center;
    max-height: 45px;
    @media #{$max-six-forty} {
      height: 5rem;
      line-height: 5rem;
      padding: 0 16px;
      flex: none;
      text-align: left; }
    &:nth-last-child(2) {
      a {
        color: c('highlight'); } }
    &:last-child {
      @media #{$max-six-forty} {
        display: none; } }
    a {
      color: c('black');
      text-decoration: none;
      &:hover {
        color: c('accent'); } } } }

.c-bottom-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  border-top: 1px solid c('spacer');
  background-color: c('white');
  z-index: 500;
  @media #{$min-six-forty} {
    display: none; } }

.c-bottom-nav__menu {
  display: flex;
  list-style: none;
  justify-content: space-around;
  align-content: stretch;
  font-size: 2.5rem;
  margin: 0 0 0 -40px;
  @supports (-webkit-padding-start: 0px) {
    -webkit-padding-start: 0px;
    margin: 0; } }


.c-bottom-nav__menu--item {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
  width: 25%;
  border-right: 1px solid c('spacer');
  padding: 0.7rem 1.5rem;
  &:last-of-type {
    span {
      margin-top: 13px; } }
  svg {
    fill: c('black');
    height: 3rem;
    &:hover {
      cursor: pointer;
      fill: c('accent'); } }
  .c-burger {
    margin-top: 15px; }
  span {
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-top: -20px;
    font-family: 'Proxima Nova', $sans;
    font-weight: 400; }
  &:hover {
    .c-burger, .c-burger:before, .c-burger:after {
      cursor: pointer;
      background-color: c('accent'); } }
  &:last-child {
    border-right: none; } }

.c-bottom-nav__menu--item.search-item {
  svg {
    margin-top: -5px;
    fill: c('black'); } }

.c-primary-header__social-icons {
  display: none;
  @media #{$min-six-forty} {
    position: absolute;
    opacity: 1;
    pointer-events: auto;
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;
    right: 0;
    top: 0;
    .c-log-in {
      padding: 8px 0;
      text-align: right;
      a {
        font-size: 12px;
        font-family: $system;
        color: c('secondary');
        font-weight: 600;
        &:hover {
          color: c('accent'); } } }
    svg {
      fill: c('spacer');
      height: 3rem;
      width: 3rem; } }
  a {
    color: c('spacer');
    padding: 0 5px;
    text-decoration: none;
    &:hover {
      svg {
        fill: c('accent'); } } } }

.search-item {
  svg {
    height: 3.6rem;
    width: 3.6rem;
    padding-bottom: 0;
    margin-top: -6px;
    margin-bottom: 0;
    fill: c('secondary'); }
  &:hover {
    cursor: pointer;
    svg {
      fill: c('accent'); } } }
