/* Featured Content Module Styling */
.o-content-row--featured {
	margin-top: $baseline * -1;
	padding-top: $grid-gutter;
	overflow: hidden; }

// Headline Module
.c-content-module--featured {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	flex: 0 1 100%;
	@media #{$min-six-forty} {
		margin-bottom: $baseline * 2; }
	.c-content-image-link {
		width: 45%;
		max-width: 600px;
		height: calc(((100vw - 30px) / 100 * 45) / 3 * 2);
		max-height: 320px;
		margin: 0 8px;
		min-width: 260px;
		flex: 1 0;
		.c-content-image {
			height: 100%;
			width: 100%;
			min-width: 100%;
			object-fit: cover; } }
	.c-content-text {
		max-width: 35%;
		min-width: 300px;
		@media #{$min-six-forty} {
			padding-left: 5%; } }
	.c-content-title {
		@media #{$max-nine-sixty} {
			@include type-setting(3);
			margin-top: 16px; }
		font-size: 3.8rem;
		line-height: 4.8rem;
		&:hover {
			color: c('black');
			text-decoration: none; } } }

.o-content-row--featured > .c-row-header {
	display: none; }

// Secondary Featured Modules - 5
.c-featured-second-row__wrapper {
	width: 90%;
	display: flex;
	align-items: flex-start;
	@media #{$max-nine-sixty} {
		flex-wrap: wrap; } }

.c-secondary-featured__wrapper {
	flex: 1 0 66%;
	display: flex;
	flex-wrap: wrap;
	@media #{$min-nine-sixty} {
		max-width: 66%; }
	// First Two Modules in Second Row
	.c-content-module--secondary-featured {
		flex: 1 0 50%;
		&:nth-of-type(even) {
			@media #{$min-six-forty} {
				border-left: 1px solid c('spacer'); } }
		&:nth-last-of-type(1) {
			@media #{$min-six-forty} {
				border-left: 1px solid c('spacer'); } }
		.c-content-image-link {
			max-height: 160px;
			margin-bottom: 20px; }
		.c-content-text {
			min-width: 96%;
			.c-content-type {
				a {
					padding: 0 0 12px;
					line-height: 1.44rem; } }
			.c-content-title {
				max-width: 96%;
				margin-left: 2%;
				@include type-setting(2); }
			.c-content-description {
				@media (max-width: 768px) {
					font-size: 1.8rem;
					line-height: 2.4rem; } } } }
	// Final Three Modules in Second Row
	.c-content-module--secondary-featured:nth-of-type(3),
	.c-content-module--secondary-featured:nth-of-type(4),
	.c-content-module--secondary-featured:nth-of-type(5) {
		flex: 1 0 33%;
		margin-top: 15px;
		border-top: 1px solid c('spacer');
		@media #{$max-six-forty} {
			display: none; }
		.c-content-image-link {
			display: none; }
		.c-content-description {
			font-size: 1.6rem;
			line-height: 2.2rem; } } }


.c-featured-content__big-sub-ad {
	@media #{$min-nine-sixty} {
		flex: 1 0 33%;
		border-left: 1px solid c('spacer');
		margin-left: 15px;
		height: 100%;
		img {
			width: 80%;
			height: auto;
			object-fit: contain;
			margin-left: 10%;
			margin-top: 30px; } }
	@media #{$max-nine-sixty} {
		min-width: 320px;
		flex: 1 0 100%;
		justify-content: center;
		max-width: 350px;
		margin: 0 auto;
		div:nth-of-type(1) {
			.jetpack-image-container {
				display: flex;
				justify-content: center;
				img {
					width: 300px;
					height: auto; } } }
		div:nth-of-type(2) {
			display: none; } } }


//
// Issue One – Featured Content
//

$thirds: 2 3 4 7 8 9 12 13 14;
$half: 5 6 10 11;
$quarters: 15 16 17 18;

.o-content-row--issue-one {
	max-width: 1280px;
	margin: 0 auto;
	margin-bottom: 6.4rem;
	border-bottom: 1px solid c('spacer'); }

.c-content-module--issue-one {
	padding-top: 3rem;
	padding-bottom: 3rem;
	@each $el in $thirds {
		&:nth-of-type(#{$el}) {
			flex: 1 0 30%; } }
	@each $el in $half {
		&:nth-of-type(#{$el}) {
			flex: 1 0 50%;
			border-top: 1px solid #fbfbfb;
			border-bottom: 1px solid #fbfbfb;
			background-color: #fbfbfb;
			margin: 0.6em 0;
			line-height: 5rem; } }
	@each $el in $quarters {
		&:nth-of-type(#{$el}) {
			flex: 1 0 25%;
			border-top: 1px solid #fbfbfb;
			border-bottom: 1px solid #fbfbfb;
			background-color: #fbfbfb;
			margin: 0.6em 0;
			line-height: 5rem; } } }

.c-content-module--notice {
	flex: 1 0 100%;
	max-width: 100%;
	border-bottom: 1px solid c('spacer');
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	padding-top: 0;
	flex-wrap: wrap; }

.c-content-module--notice__img-wrapper, .c-content-module--notice__text-wrapper {
	flex: 1 0 40%;
	max-width: 40%;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	min-width: 280px; }

.c-content-module--notice__img {
	max-height: 450px;
	max-height: 65vmin;
	max-width: 100%;
	object-fit: contain; }

.c-content-module--notice__title {
	font-size: 4.2rem;
	line-height: 5.5rem;
	border-bottom: 1px solid c('black');
	padding: 25px 0;
	font-weight: normal;
	@media (min-width: 768px) {
		margin-top: -10px;
		padding-top: 0; } }
