.u-secondary-header-wrapper {
  position: fixed;
  backface-visibility: hidden;
  top: 0;
  left: 0;
  width: 100vw;
  min-width: 100vw;
  height: 60px;
  will-change: transform;
  background: rgba(255,255,255,0);
  z-index: 9999;
  transition: box-shadow 0.5s ease-in-out; }

.c-secondary-header {
  display: flex;
  width: 100%;
  height: 100%;
  padding: 0 $grid-gutter;
  align-items: center;
  @media #{$max-six-forty} {
    justify-content: space-between; } }

.c-secondary-header__nav {
  display: flex;
  flex: 1 0 15%;
  height: 100%; }

.c-secondary-header__nav--item {
  flex: 1 0 50%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 10px;
  &:hover {
    background: c('spacer');
    cursor: pointer;
    p {
      color: c('accent'); }
    svg {
      fill: c('accent'); }
    .c-burger, .c-burger:before, .c-burger:after {
      background-color: c('accent'); } }

  svg {
    width: 4.5rem;
    padding: 0 5px;
    margin: -5px 0 0; }
  p {
    @include type-setting(-1, $sans);
    font-size: 1.2rem;
    text-transform: uppercase;
    padding: 0 5px; } }


.c-secondary-header__title {
  flex: 1 0 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  @include type-setting;
  padding-left: 40px;
  padding-top: 10px;
  justify-content: flex-start;
  height: 60px;
  a {
    text-align: center;
    svg {
      width: 17vw;
      height: auto; } }

  @media #{$max-six-forty} {
    display: none; }
  @media (max-width: 800px) {
    flex: 1 0 25%; }
  @media (max-width: 1050px) {
    flex: 1 0 50%; } }

#c-secondary-header__wordmark {
  transform: translateY(20px);
  transition: transform 0.4s ease-in-out;
  width: 30%;
  //height: 86px
  min-width: 250px;
  margin-left: -5%; }

.c-secondary-header__logo {
  @extend .c-secondary-header__nav--item;
  flex: 1 0 15%;
  padding: 0;
  align-items: center;
  &:hover {
    svg {
      stroke: c('accent'); } }
  @media #{$max-six-forty} {
    align-self: flex-end; }
  svg {
    margin-top: 0.5rem;
    width: 5rem;
    height: 5rem;
    stroke: c('black'); } }


.u-secondary-header-wrapper {
  .c-side-nav {
    position: fixed;
    left: 0;
    top: 60px;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    pointer-events: none; }
  .c-side-nav__container {
    position: relative;
    width: 85%;
    max-width: 420px;
    padding-top: 50px;
    background: c('white');
    height: 100%;
    box-shadow: 2px 0 12px rgba(0,0,0,0.4);
    transform: translateX(-102%);
    display: flex;
    flex-direction: column;
    will-change: transform; }
  .c-side-nav__menu {
    display: flex;
    flex-direction: column;
    @include type-setting(0, $sans);
    text-transform: uppercase;
    width: 100%;
    -webkit-padding-start: 0px;
    padding-top: 32px;
    flex: 1;
    list-style: none;
    padding: 0;
    margin: 0;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    max-height: 550px; }

  .side-nav--visible {
    pointer-events: auto; }

  .side-nav__menu.side-nav--visible {
    transform: translateX(-15vw); }

  .o-underlay.side-nav--visible {
    opacity: 0;
    pointer-events: none;
    @media #{$max-six-forty} {
      opacity: 1;
      pointer-events: auto; } }

  .c-side-nav__container.side-nav--animatable {
    transition: transform 0.33s cubic-bezier(0,0,0.3,1); }

  .c-side-nav__container.side-nav--animatable.side-nav--visible {
    transition: transform 0.33s cubic-bezier(0,0,0.3,1); }

  .c-side-nav__container.side-nav--visible {
    transform: none; }

  .o-underlay {
    top: 60px; } }


.c-side-nav__menu {
  height: auto;
  padding: 20px 0;
  li {
    min-height: 36px;
    @include type-setting(0, $sans);
    font-weight: 600;
    letter-spacing: 0.5px;
    @media #{$max-six-forty} {
      height: 5rem;
      line-height: 5rem;
      padding: 0 16px;
      flex: none; }
    border-bottom: 1px solid c('spacer');
    list-style: none;
    padding: 10px;
    width: 80%;
    margin: 0 auto;
    flex: 1;
    text-align: left;
    a {
      color: c('black');
      text-decoration: none;
      &:hover {
        color: c('accent'); } } } }


.c-secondary-header.is-scrolling {
  box-shadow: 0 3px 8px rgba(0,0,0,0.3);
  background-color: white;
  .c-secondary-header__nav--item {
    border-right: 1px solid c('spacer'); }
  #c-secondary-header__wordmark {
    transform: scale(0.5);
    margin-top: 8px;
    min-width: 180px; }
  .c-secondary-header__logo {
    border-right: none; } }
