$mont: $sans;

.c-archive-wrapper {
  display: flex;
  width: 100%;
  align-items: flex-start;
  flex-wrap: wrap; }

.c-archive-list {
  display: flex;
  flex-direction: column;
  background-color: white;
  z-index: 2;
  overflow: hidden;
  order: 2;
  @media (max-width: 899px) {
    min-width: 100%; }
  @media (min-width: 900px) {
    flex: 1 0 60%;
    max-width: 60vw;
    order: 1;
    margin: 80px 1vw 80px 4vw;
    box-shadow: 1px 6px 12px rgba(0,0,0,0.2); } }

.pagination__wrapper {
  display: flex;
  justify-content: space-between;
  .page-nav {
    font-size: 1.8rem;
    padding: 12px 16px;
    font-family: "proxima-nova", $sans;
    font-variant: small-caps;
    border: 1px solid c('secondary');
    margin: 28px;
    a {
      color: c('secondary');
      text-decoration: none;
      &:hover {
        color: c('primary');
        text-decoration: none; } } } }


.search-error-message {
  font-family: "proxima-nova", $sans;
  padding: 2rem;
  text-align: center;
  font-weight: 400;
  color: #68767e; }

.c-archive-filters {
  flex: 1 0 100%;
  order: 1;
  @media (min-width: 900px) {
    flex: 1 0 30%;
    max-width: 30vw;
    margin: 80px 1vw;
    order: 2; }
  @media (max-width: 900px) {
    display: flex;
    flex-wrap: wrap;
    .c-search-query-box, .c-search-box, .c-filter-list {
      flex: 1 0 100%; } } }


.c-archive-module {
  width: 100%;
  margin-top: 20px;
  padding: 0 22px 10px;
  border-bottom: 1px solid c('spacer');
  &.removed {
    display: none; } }

.c-archive-module__info {
  @media (max-width: 900px) {
    display: flex;
    flex-direction: column; } }

.c-archive-module__title {
  font-size: 2.5rem;
  line-height: 3.5rem;
  margin: 10px 0;
  color: c('black');
  font-variant: small-caps;
  font-family: "bodoni-urw", $serif;
  order: 2;
  &:hover {
    color: c('secondary');
    text-decoration: none; } }

.c-archive-module__description {
  font-family: $serif;
  font-weight: 400;
  color: #828282;
  letter-spacing: 0.3px;
  font-size: 1.5rem;
  line-height: 2.2rem;
  font-style: italic;
  margin: 10px 0;
  order: 3; }

.c-archive-module__details {
  display: flex;
  justify-content: flex-start;
  @media (max-width: 900px) {
    order: 1;
    margin: 8px 0; } }

.c-archive-module {
  .c-author-link {
    font-family: "proxima-nova", $sans;
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 2.3rem;
    margin: 0 20px 0 0;
    border: none;
    display: inline-flex;
    padding-right: 30px;
    border-right: 1px solid c('spacer');
    a {
      line-height: 1.65;
      color: #2D9CDB; }
    @media (max-width: 900px) {
      margin: 0 10px 0 0;
      padding-right: 10px; } } }


.c-archive-module__type, .c-archive-module__issue {
  font-family: "proxima-nova", $sans;
  font-weight: 600;
  color: #686e75;
  font-size: 1.1rem;
  line-height: 1.8rem;
  padding: 6px 10px;
  display: inline-flex;
  border-radius: 100px;
  margin: 0 10px;
  a {
    color: #686e75; }
  @media (max-width: 900px) {
    font-size: 1.5rem;
    line-height: 2.3rem;
    padding: 1px 0 0 0;
    margin: 0 0 0 0; }
  @media (min-width: 900px) {
    background-color: c('spacer');
    letter-spacing: 0.8px; } }

.c-archive-module__issue {
  @media (max-width: 900px) {
    display: none; } }
//
// Filters + Info
//

.c-search-query-box p {
  font-family: "proxima-nova";
  font-size: 1.5rem;
  @media (max-width: 900px) {
    text-align: center; } }

.c-search-box {
  margin-bottom: 30px;
  @media (max-width: 900px) {
    padding: 0 30px;
    form {
      margin: 0 auto; } } }

.search-field {
  height: 2rem;
  width: 60%;
  font-size: 1.4rem;
  font-family: "proxima-nova";
  border-radius: 3px;
  border: 1px solid #ddd;
  padding: 4px; }

.search-submit {
  background-color: transparent;
  border: none;
  padding: 5px 12px;
  font-family: "proxima-nova";
  font-size: 1.4rem;
  font-weight: 600;
  color: c('background-dark');
  &:hover {
    color: c('secondary'); } }

.c-filter-list {
  @media (max-width: 900px) {
    padding: 10px;
    ul {
      padding-left: 0; }
    .c-category__list-item {
      margin: 4px 0;
      padding: 0 5px;
      max-width: 90%; } } }


.c-category-list {
  @media (min-width: 900px) {
    margin: 40px 0;
    max-height: 500px;
    overflow-y: scroll; } }

.category .c-category-list {
  max-height: 40vh;
  overflow-y: scroll; }

.c-category-list__heading {
  font-size: 1.4rem;
  color: rgba(#081D29, 0.3);
  letter-spacing: 0.3px;
  @media (max-width: 900px) {
    text-align: center; } }

.c-category__list-item {
  font-size: 1.4rem;
  color: rgba(#081D29, 0.6);
  list-style: none;
  display: flex;
  justify-content: space-between;
  max-width: 50%;
  margin: 4px 0px 4px -25px;
  border-radius: 100px;
  padding: 0 12px;
  &:hover, &.selected {
    background-color: #E0E0E0;
    cursor: pointer; }
  &.c-author-name {
    max-width: 80%; } }

.c-results-sort__reverse {
  font-size: 1.4rem;
  color: rgba(#081D29, 0.6);
  background-color: rgba(#f6f9fc, 0);
  font-family: $sans;
  font-weight: 600;
  border: none;
  margin-left: 28px;
  margin-top: 20px;
  &:hover {
    color: rgba(#081D29, 0.3);
    cursor: pointer; }
  @media (max-width: 768px) {
    margin: 0 0 0 0; } }


.author-box {
  max-width: 80%;
  margin-bottom: 30px;
  @media (max-width: 900px) {
    margin: 10px auto; }
  .author-name, .page-title {
    font-size: 1.4rem;
    color: rgba(#081D29, 0.9);
    letter-spacing: 0.3px;
    font-family: "proxima-nova", $sans;
    font-weight: 600;
    @media (max-width: 900px) {
      font-size: 2.2rem; } }
  .author-description {
    font-size: 1.4rem;
    font-family: "proxima-nova", $sans;
    color: rgba(#081D29, 0.9);
    line-height: 2.3rem; } }



///
///
/// Loader /////
///
///
$lbg: #68767e;
.loader {
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: $lbg;
  background: linear-gradient(to right, $lbg 10%, rgba(76,174,255, 0) 42%);
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
  animation: load3 1.4s infinite linear;
  transform: translateZ(0); }

.loader:before {
  width: 50%;
  height: 50%;
  background: $lbg;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: ''; }

.loader:after {
  background: #ffffff;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0; }

@-webkit-keyframes load3 {
  0% {
    transform: rotate(0deg); }

  100% {
    transform: rotate(360deg); } }


@keyframes load3 {
  0% {
    transform: rotate(0deg); }

  100% {
    transform: rotate(360deg); } }

