.u-page-wrapper {
  display: flex;
  flex-wrap: wrap;
  padding: 0 0;
  @media #{$min-six-forty} {
    padding: 0 $grid-gutter; } }

.u-page-wrapper--primary-header {
  margin: 0 2vw 0;
  @media #{$min-six-forty} {
    margin: 0 $grid-unit 0; } }

.u-page-wrapper--secondary-header {
  margin: 84px $grid-gutter 0;
  @media #{$min-six-forty} {
    margin: 168px $grid-unit 0; }
  @supports (display: grid) {
    margin: 10vh 0 0; } }

.u-page-wrapper--category {
  margin: 0 auto;
  background-color: #f6f9fc; }
.u-page-wrapper--full-width {
  padding: 0;
  margin-left: 0;
  margin-right: 0; }

main {
  width: 100%; }

.o-article {
  padding: $grid-gutter;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: $serif;
  p {
    width: 100%; }
  @media #{$min-nine-sixty} {
    flex: 1 0 62%;
    max-width: 62%;
    width: 62%;
    margin: 0 auto; }
  @supports (display: grid) {
    width: 100%;
    display: grid;
    max-width: none;
    grid-template-columns: 1fr minmax(300px, 780px) 1fr;
    h1, h2, h3, h4, h5, h6, p, table, form, ul, ol, figure, blockquote, footer, .woocommerce {
      grid-column: 2; } } }

.c-hp-widget {
  margin-top: 3.15rem;
  &:first-of-type {
    margin-top: 0; } }


//
// Subscriptions Page
//

.page-id-184 {
  @import "modules/components/_subscription-info";
  @import "modules/components/_stripe-form"; }

#card-errors {
  text-align: center;
  grid-column: none;
  font-family: "Proxima Nova", $sans, $system;
  font-size: 16px;
  color: red; }

.spinner {
  position: absolute;
  top: 0;
  left: 0;
  transition: opacity 0.25s ease-in;
  opacity: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  &.invisible {
    display: none; }
  &.animate-in {
    display: flex; }
  &.animate-out {
    opacity: 0; }
  &.visible {
    display: flex;
    opacity: 1; }
  p {
    text-align: center;
    grid-column: none;
    font-family: "Proxima Nova", $sans, $system;
    font-size: 16px;
    color: c('secondary'); } }

.sk-folding-cube {
  margin: 20px auto;
  width: 40px;
  height: 40px;
  transform: rotateZ(45deg);
  .sk-cube {
    float: left;
    width: 50%;
    height: 50%;
    position: relative;
    transform: scale(1.1);
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: c('primary');
      animation: sk-foldCubeAngle 2.4s infinite linear both;
      transform-origin: 100% 100%; } }
  .sk-cube2 {
    transform: scale(1.1) rotateZ(90deg);
    &:before {
      animation-delay: 0.3s; } }
  .sk-cube3 {
    transform: scale(1.1) rotateZ(180deg);
    &:before {
      animation-delay: 0.6s; } }
  .sk-cube4 {
    transform: scale(1.1) rotateZ(270deg);
    &:before {
      animation-delay: 0.9s; } } }

@keyframes sk-foldCubeAngle {
  0%, 10% {
    transform: perspective(140px) rotateX(-180deg);
    opacity: 0; }
  25%, 75% {
    transform: perspective(140px) rotateX(0deg);
    opacity: 1; }
  90%, 100% {
    transform: perspective(140px) rotateY(180deg);
    opacity: 0; } }

#success-container, #failure-container {
  display: block;
  &.invisible {
    display: none; }
  h3 {
    font-size: 2.2rem;
    text-align: center; }
  p {
    text-align: center;
    grid-column: none;
    font-family: "Proxima Nova", $sans, $system;
    font-size: 14px;
    line-height: 22px;
    a {
      color: c('primary');
      &:hover {
        color: c('secondary'); } } } }



//
//
// Account Page
//
//

.woocommerce-account {
  .site-content {
    max-width: 960px;
    margin: 0 auto;
    .site-main {
      display: flex;
      flex-wrap: wrap;
      padding: 10px;
      section {
        border-bottom: 1px solid c('spacer');
        padding: 0 0 20px;
        margin-bottom: 20px; }
      section, h1 {
        flex: 1 0 100%; }
      section.half-width {
        flex: 1 0 50%; }
      h1 {
        font-family: $sans; }
      h2 {
        font-size: 2rem;
        font-family: $sans; }
      p {
        font-size: 1.6rem;
        line-height: 2.1rem;
        max-width: 420px;
        &.message {
          color: c('primary'); }
        &.error-message {
          color: c('highlight'); } }
      li {
        font-size: 1.6rem;
        line-height: 2.5rem; }
      button {
        background-color: c('primary');
        border: none;
        color: white;
        font-family: $sans;
        letter-spacing: 0.4px;
        padding: 11px 16px 8px;
        font-size: 1.2rem;
        border-radius: 2px;
        margin: 5px 0;
        display: block;
        box-shadow: 0 2px 3px rgba(0,0,0,0.3);
        transition: all 0.1s ease-in-out;
        &:hover {
          transform: translateY(-1px);
          box-shadow: 0 3px 6px -2px rgba(0,0,0,0.25);
          background-color: c('accent'); } } } }
  .account-page__form {
    background-color: #fbfbfb;
    padding: 10px;
    max-width: 440px; }
  .account-page__button {
    display: flex;
    justify-content: center; }
  .account-page__form-item {
    display: flex;
    flex-direction: column;
    max-width: 420px;
    label {
      font-family: $sans;
      font-size: 1.2rem;
      font-weight: bold; }
    input {
      font-size: 1.4rem;
      font-family: $sans;
      padding: 3px;
      border: 1px solid c('spacer');
      margin: 4px 0 10px; } } }

