.woocommerce-page {
  aside {
    display: none; } }

.woocommerce {
  input, label {
    @include type-setting(0, $sans, c('black')); }
  .button {
    @extend %button;
    @include o-button; }

  .woocommerce-breadcrumb, .woocommerce-result-count, .woocommerce-pagination {
    display: none; }

  .c-shop-header-wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    margin: -20px 0 0;
    border-bottom: 1px solid c('spacer');
    padding: 0 0 $baseline; }


  .page-title {
    @include type-setting(4, $serif, c('black'));
    display: block;
    padding: 5px 16px;
    flex: 1 0 33%;
    min-width: 280px;
    text-align: center;
    font-style: italic;
    margin: 0 0; }

  .c-shop-header__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1 0 33%;
    min-width: 280px; }

  .c-shop-header__nav__title {
    @include type-setting(0, $sans, c('secondary')); }

  .c-shop-header__nav__links {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    a {
      @extend %button;
      @include o-button;
      flex: 1 0 40%;
      margin: 10px;
      text-align: center;
      &:hover {
        text-decoration: none; } } }

  .woocommerce-ordering {
    background: rgba(0,0,0,0);
    @include type-setting(-1, $sans, c('black')); }

  ul.products {
    width: 100%;
    .c-products {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: stretch;
      @media #{$max-six-forty} {
        justify-content: center; } }
    li.product {
      flex: 1 0 30%;
      max-width: 30%;
      min-width: 225px;
      margin: 30px 15px;
      padding: 0;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      @media #{$max-six-forty} {
        max-width: 100%;
        display: flex;
        align-items: center;
        .c-product__thumb, .c-product__text {
          margin: 0 10px; } }
      &:nth-of-type(3n + 2) {
        @media #{$min-twelve-eighty} {
          border-left: 1px solid c('spacer');
          border-right: 1px solid c('spacer');
          padding-right: 15px; } }
      &:last-of-type {
        border-right: none; }
      .c-product__thumb {
        width: 40%;
        margin: 0 auto;
        padding: 0 10px;
        @media #{$min-six-forty} {
          width: 50%; }
        img {
          max-width: 100%;
          object-fit: contain;
          min-width: 50px;
          max-width: 150px;
          margin: 0 auto;
          @media #{$max-six-forty} {
            max-height: 140px; } } }
      .woocommerce-loop-product__title {
        text-align: center;
        font-size: 2rem;
        line-height: 2.65rem;
        color: c('black');
        font-family: "Proxima Nova", $sans;
        a {
          color: c('black'); }
        &:after {
          display: none; } }
      .c-product__text {
        width: 60%;
        margin: 0 auto;
        @media #{$min-six-forty} {
          width: 50%; } }
      .c-product-loop__issue-volume {
        font-size: 1.28rem;
        line-height: 1.8rem;
        font-weight: 400;
        color: rgba(33, 37, 41, 0.72);
        font-family: "Proxima Nova", $sans;
        margin: 0 0;
        text-align: center; }
      .c-product-loop__author {
        text-align: center;
        font-size: 1.44rem;
        line-height: 1.9rem;
        color: rgba(33, 37, 41, 0.72);
        font-family: "Proxima Nova", $sans;
        font-weight: 400;
        margin: 0 0 5px;
        a {
          color: rgba(33, 37, 41, 0.72); }
        &:after {
          content: '';
          width: 20%;
          height: 1px;
          position: relative;
          display: block;
          top: 1px;
          left: 40%;
          background-color: c('spacer'); } }
      .price {
        text-align: center;
        font-size: 1.28rem;
        line-height: 2.8rem;
        color: rgba(33, 37, 41, 0.72);
        font-family: "Proxima Nova", $sans;
        font-weight: 400;
        margin-bottom: 0; }
      .button {
        @extend %button;
        @include o-button;
        display: block;
        max-width: 150px;
        text-align: center;
        margin: 0 auto;
        border: none;
        border-bottom: 2px solid c('primary');
        font-size: 1.2rem; } } }



  div.product {
    form.cart {
      .variations {
        max-width: 400px;
        td.label {
          padding-top: 2px; }
        td {
          vertical-align: middle;
          padding-bottom: 5px; } } }
    .images {
      img {
        width: auto !important;
        max-width: 300px;
        margin: 0 auto;
        @media (min-width: 960px) {
          max-width: 400px; } } }

    .c-product-page__author {
      font-size: 1.6rem;
      line-height: 3.6rem;
      font-family: "Proxima Nova", $sans;
      color: rgba(33, 37, 41, 0.72);
      margin-bottom: $baseline / 2; }

    .c-product-page__issue-volume {
      font-size: 1.6rem;
      line-height: 3.6rem;
      font-family: "Proxima Nova", $sans;
      color: rgba(33, 37, 41, 0.72); }

    .product_title {
      font-family: $sans;
      font-size: 3.5rem;
      line-height: 5rem; }
    p.price {
      .woocommerce-Price-amount {
        @include type-setting(1, $sans, c('secondary'));
        font-family: "Proxima Nova", $sans;
        font-weight: 400;
        color: rgba(33, 37, 41, 0.72); } }

    .quantity {
      @include type-setting(0, $sans, c('black'));
      .qty {
        height: 36px; } }

    div[itemprop="description"] {
      display: none; }

    p.stock {
      @include type-setting(0, $sans, c('black')); }

    .single_add_to_cart_button.button.alt {
      @extend %button;
      @include o-button;
      margin: 0; }

    .product_meta {
      display: none; }

    .product-info {
      p {
        @include type-setting(-1, $sans, c('secondary')); } }

    ul.c-product-page__meta-info {
      list-style: none;
      li {
        margin-left: -40px;
        color: c('secondary');
        font-family: "Proxima Nova", $sans;
        font-size: 1.28rem;
        line-height: 2.8rem; } }

    .c-product-page__pullquote-quote {
      font-style: italic;
      max-width: 85%;
      &:before {
        content: '"'; }
      &:after {
        content: '"'; } }

    .c-product-page__pullquote-attrib {
      max-width: 85%;
      text-align: right;
      margin-top: -1rem; }

    .woocommerce-variation-price {
      padding-bottom: 2rem;
      font-size: 2.5rem;
      .price {
        color: c('primary');
        font-family: "Proxima Nova", $sans; } }

    .product-description {
      padding: 20px 0;
      width: 80%;
      &:before {
        content: '';
        display: block;
        position: relative;
        top: 0;
        left: 0;
        height: 1px;
        width: 100%;
        background: c('spacer'); } }

    .wc-tabs-wrapper {
      max-width: 700px;
      margin: 0 auto 100px;
      border-bottom: 1px solid c('spacer'); }

    .woocommerce-tabs ul.tabs.wc-tabs {
      display: none; }

    .woocommerce-Tabs-panel {
      display: flex !important;
      flex-direction: column;
      align-items: center;
      border-top: 1px solid c('spacer');
      h2 {
        display: none; }
      h3 {
        display: inline-block;
        @include type-setting(2, $serif, c('black'));
        &:after {
          content: '';
          display: block;
          position: relative;
          top: 0;
          left: 0;
          height: 1px;
          width: 100%;
          background: c('secondary'); } }
      p {
        text-align: left; } }

    .woocommerce-tabs {
      ul.tabs {
        li.active {
          border: none;
          background: rgba(0,0,0,0);
          width: 100%;
          text-align: center;
          &:before,
          &:after {
            content: none; } }
        .description_tab {
          a {
            @include type-setting(3, $serif, c('black'));
            text-align: center; } } }
      .c-table-of-contents {
        margin-top: 0; } }

    .c-table-of-contents__section {
      h2 {
        display: block; }
      .c-table-of-contents__section-header {
        font-size: 2.3rem;
        font-family: $sans; }
      .c-table-of-contents__list {
        list-style: none;
        text-align: left; }
      .c-table-of-contents__link {
        @include type-setting(0, $serif);
        font-weight: normal;
        .c-author-link {
          padding-right: 0; }
        a {
          @include type-setting(0, $serif);
          font-weight: 400; } } } }

  .shop_attributes {
    display: none; }

  .related.products, .upsells.products {
    clear: both;
    padding: 40px 0 20px;
    h2:first-child {
      font-family: "Proxima Nova", $sans;
      color: c('black');
      font-size: 2rem;
      width: 100%;
      text-align: center;
      letter-spacing: 1px;
      margin-bottom: 20px;
      &:after {
        content: '';
        display: block;
        position: relative;
        top: 8px;
        left: 0;
        height: 1px;
        width: 100%;
        background: c('spacer'); } }
    ul.products {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      li.product {
        flex: 1 0 25%;
        margin: 0 0;
        min-width: 150px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        border: none;
        @media #{$max-six-forty} {
          flex-direction: column;
          align-items: center; }
        .c-product__thumb {
          height: 120px;
          margin: 0 auto;
          img {
            max-height: 100%;
            min-width: 50px; } }
        .c-product__text {
          min-width: 120px;
          .c-product-loop__author, .c-product-loop__issue-volume {
            font-size: 1.18rem;
            line-height: 1.8rem;
            padding-bottom: 5px; }
          .c-product-loop__author a, .c-product-loop__issue-volume a {
            color: c('secondary'); }
          h2 {
            @media #{$min-six-forty} {
              @include type-setting(-1, $sans, c('black'));
              font-size: 1.58rem;
              line-height: 2rem;
              padding: 0 0 0 0; }
            a {
              color: c('black');
              line-height: 2rem; } }
          .price {
            display: none;
            @media #{$min-six-forty} {
              @include type-setting(-1, $sans, c('secondary'));
              letter-spacing: 0.5px; } } }
        .button {
          @media #{$min-six-forty} {
            @include type-setting(-1, $sans, c('primary'));
            font-size: 1.2rem;
            line-height: 2.2rem;
            margin-top: 10px;
            &:hover {
              @include type-setting(-1, $sans, c('white'));
              font-size: 1.2rem;
              line-height: 2.2rem; } }
          padding-top: 5px;
          width: 100%;
          max-width: 150px;
          min-width: 120px; } } } }



  .woocommerce-message {
    @include type-setting(0, $serif, c('black'));
    border-top-color: c('primary');
    background-color: c('background-light');
    &:before {
      color: c('primary'); }
    a {
      @extend %button;
      @include o-button;
      margin: 0 20px; } }

  .shop_table {
    background-color: white;
    @include type-setting(0, $sans);
    th {
      @include type-setting(0, $sans, c('secondary')); }
    tr {
      @include type-setting(0, $sans); }
    @include type-setting(0);
    .actions {
      @media #{$max-six-forty} {
        display: flex;
        flex-direction: column;
        .coupon {
          display: flex;
          flex-direction: column;
          align-items: center;
          input {
            margin: 10px 0; } } }
      #coupon_code {
        @include type-setting(-1, $sans);
        width: 80px; }
      .button {
        @extend %button;
        @include o-button;
        margin: 0 20px;
        @media #{$max-six-forty} {
          width: auto !important; }
        &:hover {
          box-shadow: none; } } } }

  .shipping-calculator-button {
    @include type-setting(0, $sans, c('primary')); }

  .variation-Edition {
    @include type-setting(0, $sans, c('black'));
    p {
      @include type-setting(0, $sans, c('black'));
      margin-top: 0; } }

  .wc-proceed-to-checkout {
    .checkout-button.button.alt.wc-forward {
      @extend %button;
      @include o-button(c('primary'), c('white'));
      margin: 0 20px;
      &:hover {
        border-color: c('primary'); } } }
  .woocommerce-shipping-destination {
    font-family: "Proxima Nova", $sans, $system;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    padding: 0; }

  .woocommerce-checkout-payment {
    .button.alt {
      @extend %button;
      @include o-button;
      width: 30%;
      min-width: 300px;
      height: 72px;
      @include type-setting(2, $sans, c('primary'));
      @media #{$max-six-forty} {
        @include type-setting(1, $sans, c('primary'));
        min-width: 260px; } } }

  form {
    input.input-text {
      font-family: "Proxima Nova", $sans;
      font-size: 14px;
      padding: 5px; }
    .form-row.woocommerce-validated {
      input.input-text {
        border: 3px solid;
        border-color: c('primary'); } }
    .woocommerce-invalid {
      input.input-text {
        border: 3px solid;
        border-color: c('highlight'); } }
    .selection {
      font-family: "Proxima Nova", $sans;
      font-size: 14px; } } }


// Floating WooCommerce Cart
.c-woo-cart {
  position: fixed;
  bottom: 30px;
  right: 30px;
  padding: 10px;
  text-align: center;
  background-color: rgba( c('spacer'), 0.6);
  h3 {
    @include type-setting(0, $sans, c('secondary'));
    line-height: 1.5rem;
    display: inline-block;
    margin: 0 0;
    svg {
      width: 18px;
      height: 14px;
      fill: c('secondary'); } }
  a {
    @include type-setting(0, $sans, c('primary'));
    line-height: 1.5rem; } }


.wc_payment_method {
  p {
    font-size: 14px;
    font-family: "Proxima Nova", $sans;
    margin: 0 0 0 0; } }

.woocommerce-billing-fields, .woocommerce-additional-fields {
  p {
    font-size: 14px;
    font-family: "Proxima Nova", $sans;
    margin: 0 0 0 0; } }

#wc-stripe-payment-request-button-separator {
  font-size: 1.28rem;
  line-height: 2.8rem;
  font-family: "Proxima Nova", $sans;
  color: rgba(33, 37, 41, 0.72);
  text-align: left !important; }

#wc-stripe-payment-request-wrapper {
  max-width: 280px; }


// Disabled Styling
.woocommerce #respond input#submit.disabled, .woocommerce #respond input#submit:disabled, .woocommerce #respond input#submit:disabled[disabled], .woocommerce a.button.disabled, .woocommerce a.button:disabled, .woocommerce a.button:disabled[disabled], .woocommerce button.button.disabled, .woocommerce button.button:disabled, .woocommerce button.button:disabled[disabled], .woocommerce input.button.disabled, .woocommerce input.button:disabled, .woocommerce input.button:disabled[disabled] {
  padding: 3px 16px 0; }
