.content-row--news {
	min-height: 300px;
	&:after {
		content: "";
		flex: auto; } }

.news__wrapper {
	@include pattern;
	width: 100%;
	padding: $baseline;
	display: flex;
	flex-flow: column nowrap; }

.news-module {
	width: 100%;
	min-width: 225px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 10px;
	background-color: c('white');
	margin: 5px;
	@media #{$min-six-forty} {
		max-width: 90%;
		margin: $grid-gutter 5%;
		box-shadow: 0 3px 8px rgba(0,0,0,0.3); } }

.news-module__image {
	height: 86px;
	width: 86px;
	min-height: 86px;
	min-width: 86px;
	margin-left: $grid-gutter;
	img {
		width: 100%;
		height: 100%;
		object-fit: cover; } }

.news-text {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
	max-width: calc(100% - 100px); }

.news-title {
	@include type-setting(-1);
	display: block;
	text-decoration: none;
	@include underlineHover(currentColor, 1px, 100%);
	max-width: 100%;
	&:after {
		transform-origin: top left; }
	@media #{$min-twelve-eighty} {
		@include type-setting(0); }
	&:hover {
		color: c('black');
		text-decoration: none; } }

.news-info {
	line-height: 1.8rem;
	width: calc(100% + 80px); }

.news-type {
	display: inline-block;
	@include type-setting(-1, $sans);
	margin: 10px 0 0;
	a {
		@include type-setting(-1, $sans);
		border-right: 1px solid c('spacer');
		padding-right: 8px;
		letter-spacing: 0.5px;
		text-decoration: none;
		text-transform: uppercase;
		font-size: 14px;
		&:hover {
			text-decoration: underline; } } }


.news-time {
	display: inline-block;
	@include type-setting(-1, $sans, c('secondary'));
	text-decoration: none;
	padding-left: 5px;
	margin: 0;
	font-size: 14px; }
