*, *:before, *:after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 11px;
  background-color: c('white');
  overflow-x: hidden;
  max-width: 100vw;
  @media #{$max-nine-sixty} {
    font-size: 10px; } }

body {
  margin: 0;
  width: 100vw;
  overflow-x: hidden;
  background-color: c('white');
  color: c('black'); }

p {
  @include type-setting(0, $serif); }

a {
  color: c('primary');
  text-decoration: none;
  &:hover {
    color: c('accent');
    text-decoration: none; } }

a.c-author-link {
  color: c('black');
  text-decoration: none;
  &:hover {
    color: c('black');
    text-decoration: underline; } }

@supports (display: grid) {
  .c-article__body hr {
    grid-column: 2;
    min-width: 96%; } }

