.o-article--info {
  h1,h2,h3,h4,h5 {
    font-family: $sans;
    @media #{$max-six-forty} {
      font-size: 22px; } }

  h1 {
    line-height: 60px;
    margin: 24px 0 12px;
    font-size: 4.5rem;
    @media #{$max-six-forty} {
      line-height: 32px;
      font-size: 2.5rem;
      margin: 0 0; }
    &:after {
      content: '';
      display: inline-block;
      position: relative;
      top: -20px;
      left: 0;
      height: 2px;
      width: 100%;
      background: c('primary'); } }
  h2 {
    font-size: 3.8rem;
    line-height: 4.5rem;
    margin: 24px 0 12px;
    @media #{$max-six-forty} {
      line-height: 32px;
      font-size: 2.5rem;
      margin: 0 0; } }

  h3, h4, h5 {
    font-size: 3.4rem;
    margin: 2.4rem 0 1rem; } }
