.o-content-row--magazine {
  @include pattern(c('accent'));
  @media #{$min-nine-sixty} {
    max-height: 450px; } }

.c-content-module--magazine {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex: 0 1 100%;
  margin: 0 0; }

.c-mag-home__image {
  flex: 1 0 40%;
  min-width: 280px;
  display: flex;
  justify-content: center;
  a {
    img {
      max-height: 300px;
      object-fit: contain; } } }


.c-mag-home__text--wrapper {
  flex: 1 0 60%; }

.c-mag-home__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(c('white'), 1);
  padding: 30px 0;
  max-width: 550px;
  margin: 0 auto;
  box-shadow: 0 10px 25px rgba(0,0,0,0.2);
  .c-mag-issue {
    @include type-setting(0, $sans, c('secondary'));
    &:after {
      content: '';
      display: block;
      position: relative;
      top: -4px;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: c('secondary'); } } }


.c-mag-home__issue-title {
  text-align: center;
  a {
    @include type-setting(4, $serif, c('black'));
    @include underlineHover(currentColor, 1px, 80%);
    @media #{$max-nine-sixty} {
      font-size: 3rem;
      line-height: 3.8rem; }
    &:hover {
      color: c('primary');
      text-decoration: none; } } }


.c-mag-home__issue-contents {
  max-width: 80%;
  p {
    @include type-setting(0, $serif, c('black'));
    font-style: italic;
    font-weight: 400; } }

.c-mag-home__button-wrapper {
  display: flex;
  width: 80%;
  max-width: 400px;
  justify-content: space-between;
  margin-top: 1.5rem; }


.c-mag-home__button {
  background-color: rgba(0,0,0,0);
  margin: 0 10px;
  @media #{$max-six-forty} {
    text-align: center;
    @include type-setting(-1, $sans); }
  &:hover {
    a {
      color: c('white');
      text-decoration: none; } } }
