.c-podcast-player {
  border: 1px solid c('primary');
  background-color: c('white');
  box-shadow: 0px 12px 24px rgba(black, 0.3);
  display: flex;
  width: 100%;
  min-width: 280px;
  min-height: 160px;
  justify-content: space-between;
  align-items: flex-start;
  overflow: hidden;
  position: relative;
  @supports (display: grid) {
    grid-column: 2; } }

.c-podcast-player__image {
  height: 160px;
  @media (max-width: 600px) {
    display: none; } }

.c-podcast-player__audio-element {
  padding: 10px; }

.c-podcast-player__elements-wrapper {
  display: flex;
  flex-wrap: wrap; }

.js-podcast-player__play-pause {
  height: 80px;
  width: 80px;
  margin: 10px 10px;
  @media (max-width: 700px) {
    height: 55px;
    width: 55px; } }

.js-podcast-player__play-pause svg {
  height: 100%;
  width: 100%;
  border: 5px solid c('primary');
  border-radius: 100%;
  fill: c('primary');
  #Pause {
    display: none; }
  &:hover {
    cursor: pointer;
    fill: c('white');
    background-color: c('primary'); } }

.js-podcast-player__play-pause svg.is-playing {
  #Pause {
    display: block;
    transform: translateX(-10px); }
  #Play {
    display: none; } }

.c-podcast-player__title {
  @include type-setting(0, $sans, c('black'));
  margin: 0 0;
  padding: 10px 10px;
  line-height: 2rem;
  height: auto;
  width: calc(100% - 100px);
  @media (max-width: 700px) {
    width: calc(100% - 75px); }
  h2 {
    margin: 10px 0 0; }
  .c-podcast-player__series-title {
    font-size: 12px;
    line-height: 18px;
    color: c('accent'); } }

.c-podcast-player__info {
  display: flex;
  flex: 1 0 100%;
  max-width: 100%;
  justify-content: space-between;
  align-items: center;
  font-family: $sans;
  font-size: 1.4rem;
  line-height: 1.4rem;
  color: c('secondary');
  padding: 0 10px 0 110px;
  margin-top: -30px;
  flex-wrap: wrap;
  @media (max-width: 700px) {
    padding: 0 10px;
    margin-top: 0; }
  .c-podcast-player__progress-bar {
    flex: 1 0 100%;
    width: 100%;
    display: block;
    height: 10px;
    border: 1px solid c('primary');
    margin-bottom: 10px;
    position: relative;
    .c-podcast-player__progress-bar--overlay {
      position: absolute;
      left: 0;
      top: 0;
      height: 8px;
      width: 100%;
      background-color: c('primary');
      transform: scaleX(0);
      transform-origin: left; } }
  .c-podcast-player__progress {
    @media (max-width: 700px) {
      margin-bottom: 30px; } }
  .c-podcast-player__volume-wrapper {
    @media (max-width: 700px) {
      display: none; }
    p {
      margin: 0 0;
      display: inline;
      font-family: $sans;
      font-size: 1.4rem;
      line-height: 1.4rem;
      color: c('secondary'); } }
  #vol-control {
    margin: 0 10px 0;
    vertical-align: middle;
    max-width: 60px; }
  .c-podcast-player__download-button {
    @extend %button;
    @include o-button;
    font-size: 12px;
    line-height: 16px;
    height: 26px;
    padding: 4px 8px;
    margin: 0 10px;
    max-width: 80px;
    @media (max-width: 700px) {
      display: none; }
    &:hover {
      text-decoration: none;
      box-shadow: none; } } }

.c-podcast-player__slide {
  position: absolute;
  left: 160px;
  top: 0;
  height: 100%;
  width: calc(100% - 160px);
  display: block;
  background-color: c('white');
  padding: 10px;
  transform: translateY(100%);
  transform-origin: bottom;
  transition: transform 0.3s ease-in-out;
  @media (max-width: 700px) {
    width: 100%;
    left: 0; } }

.c-podcast-player__slide.is-visible {
  transform: translateY(0); }

.c-podcast-player__button-bar {
  position: absolute;
  bottom: 0;
  left: 160px;
  width: calc(100% - 160px);
  height: 24px;
  font-size: 12px;
  line-height: 16px;
  font-family: $sans;
  padding: 4px 8px;
  border-top: 1px solid c('primary');
  background-color: c('primary');
  color: c('white');
  display: flex;
  justify-content: flex-end;
  z-index: 100;
  @media (max-width: 700px) {
    width: 100%;
    left: 0; }
  span {
    margin: 0 12px;
    &:hover {
      cursor: pointer;
      text-decoration: underline; } } }

.c-podcast-player__slide--text {
  @include type-setting(1, $sans, c('secondary'));
  clear: right;
  display: inline-block;
  padding-bottom: 5px;
  border-bottom: 1px solid c('spacer');
  max-width: calc(100% - 45px);
  @media (max-width: 700px) {
    @include type-setting(0, $sans, c('secondary')); }
  .c-podcast-player__slide--text__podcast-title {
    color: c('primary'); } }

.o-close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  svg {
    height: 25px;
    width: 25px;
    fill: c('secondary');
    opacity: 0.6;
    &:hover {
      fill: c('primary');
      opacity: 1;
      cursor: pointer; } } }

.c-podcast-player__slide--links {
  padding-top: 10px; }

.c-podcast-player__share-icon {
  &:hover {
    text-decoration: none; }
  svg {
    height: 30px;
    width: 30px;
    fill: c('accent');
    &:hover {
      fill: c('primary');
      cursor: pointer; } } }
